import {
    Component,
    h,
    Prop,
    Event,
    EventEmitter,
    Host,
    Element,
    Watch,
} from '@stencil/core';

export interface ButtonGroupOption {
    option_id: string;
    label?: string;
    icon?: string;
    is_active?: string;
    disabled?: boolean;
    readonly?: boolean;
}

/**
 * @category Form Controls
 * @description A button group component that allows single or multiple selection from a set of toggle options. Provides a cohesive way to group related action buttons with shared styling and behavior.
 *
 * @slot - used to place your own at-button-group-options if 'options' prop isn't set. Parent will manage button selection and emit change events.
 */
@Component({
    tag: 'at-button-group',
    shadow: false,
})
export class AtButtonGroup {
    /**
     * Label for button group.
     */
    @Prop()
    label?: string;

    /**
     * Optional info icon with detailed tooltip description.
     *
     * Displayed at right of label.
     */
    @Prop()
    info_text?: string;

    /**
     * Hint for options.
     */
    @Prop()
    hint_text?: string;

    /**
     * Error text for invalid choices.
     */
    @Prop()
    error_text?: string;

    /**
     * List of options to be displayed on the button group.
     */
    @Prop()
    options: ButtonGroupOption[] = [];

    /**
     * Sets the current active button
     */
    @Prop({ mutable: true })
    value: string;

    /**
     * Disables the button group and prevents interaction
     */
    @Prop()
    disabled?: boolean;

    @Element() el: HTMLAtButtonGroupElement;
    private buttonGroupId = `buttonGroup-${Math.random().toString(36).substring(2, 11)}`;

    /**
     * When the active button is changed, this will emit the 0-based index of the active button
     */
    @Event()
    atuiIndexChange: EventEmitter<number>;

    /**
     * When the active button is changed, this will emit the text value of the active button
     */
    @Event()
    atuiChange: EventEmitter<string>;

    buttonEls: HTMLAtButtonGroupOptionElement[] = [];

    @Watch('value')
    handleValueChange(newValue: string) {
        if (!this.options || this.options.length === 0) {
            this.buttonEls.forEach((child) => {
                child.is_active = child.option_id === newValue;
            });
        }
    }

    componentDidLoad() {
        if (!this.options || this.options.length === 0) {
            this.buttonEls = this.getButtonElements();
            this.initializeButtons();
            this.attachEventListenersToButtons();
            setTimeout(() => this.activateOptionButton());
        }
        this.el.addEventListener(
            'keydown',
            this.handleKeyDown as EventListener,
        );
    }

    getButtonElements() {
        return Array.from(
            this.el.querySelectorAll('at-button-group-option'),
        ) as HTMLAtButtonGroupOptionElement[];
    }

    initializeButtons() {
        this.buttonEls.forEach((buttonEl, index) => {
            buttonEl.option_id = buttonEl.option_id || `${index}`;
            if (this.value !== undefined && this.value !== null) {
                buttonEl.is_active = this.value === buttonEl.option_id;
            }
            buttonEl.disabled = this.disabled || buttonEl.disabled;
        });
    }

    attachEventListenersToButtons() {
        this.buttonEls.forEach((buttonEl, index) => {
            buttonEl.addEventListener('atuiClick', (event) =>
                this.handleChange(event, buttonEl.option_id, index),
            );
        });
    }
    activateOptionButton() {
        const activeChild = this.buttonEls.find(
            (child) => child.is_active && !child.disabled,
        );
        const initialButton =
            activeChild || this.buttonEls.find((child) => !child.disabled);
        if (initialButton) {
            initialButton.is_active = true;
            this.value = initialButton.option_id;
        }
    }

    get getButtonGroupOptions(): any {
        if (this.options) {
            return this.options.map((option, index) => (
                <at-button-group-option
                    option_id={option.option_id}
                    label={option.label}
                    is_active={this.value === option.option_id}
                    disabled={option.disabled}
                    icon={option.icon}
                    onAtuiClick={(event) =>
                        this.handleChange(event, option.option_id, index)
                    }
                />
            ));
        }
        return null;
    }

    handleChange(event: CustomEvent, optionId: string, index: number): void {
        event.stopPropagation();
        this.value = optionId;
        this.atuiChange.emit(this.value);
        this.atuiIndexChange.emit(index);
        this.buttonEls.forEach((child) => {
            child.is_active = child.option_id === optionId;
        });
    }

    /**
     * Handles keyboard navigation for all button options.
     */
    private handleKeyDown = (evt: KeyboardEvent) => {
        if (evt.key !== 'Enter' && evt.key !== ' ') return;
        const target = evt.target as HTMLElement;
        if (target.tagName !== 'BUTTON') return;

        const option = target.closest(
            'at-button-group-option',
        ) as HTMLAtButtonGroupOptionElement;

        if (!option || option.disabled) return;

        evt.preventDefault();

        const index =
            this.options?.length > 0
                ? this.options.findIndex(
                      (opt) => opt.option_id === option.option_id,
                  )
                : this.buttonEls.indexOf(option);

        if (index >= 0) {
            const customEvent = new CustomEvent('atuiClick', {
                detail: { element: option },
            });
            this.handleChange(customEvent, option.option_id, index);
        }
    };

    disconnectedCallback() {
        this.el.removeEventListener(
            'keydown',
            this.handleKeyDown as EventListener,
        );
    }

    render() {
        return (
            <Host
                role="radiogroup"
                aria-labelledby={this.buttonGroupId}
                class={'flex flex-col items-start'}
            >
                <div class="flex flex-col">
                    <slot name="label" />
                    {(this.label || this.info_text) && (
                        <at-form-label
                            id={this.buttonGroupId}
                            label={this.label}
                            info_text={this.info_text}
                        />
                    )}
                    {this.hint_text && (
                        <span
                            class="text-light mb-8 inline-block text-xs leading-tight"
                            data-name="button-group-hint"
                        >
                            {this.hint_text}
                        </span>
                    )}
                </div>
                <div class="border-med relative rounded-lg border bg-white inset-shadow-xs">
                    <ul
                        class="relative z-20 m-[3px] flex flex-row"
                        data-name="button-group-options"
                    >
                        <slot />
                        {this.getButtonGroupOptions &&
                            this.getButtonGroupOptions.map((button) => (
                                <li class="relative z-10 mr-[-1px]">
                                    {button}
                                </li>
                            ))}
                    </ul>
                </div>
                {this.error_text && (
                    <span
                        class="text-error text-xs font-medium"
                        data-name="button-group-error-text"
                    >
                        {this.error_text}
                    </span>
                )}
            </Host>
        );
    }
}
